@import url("./heder.scss");
@import url("./buner.scss");
@import url("./app.scss");
@import url("./speakers.scss");
@import url(./help.scss);
@import url(./advantage.scss);
@import url(./steps.scss);




 * {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

h1 {
	font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 120%;
    display: flex;
    align-items: center;
    color: #0E0E0F;
    flex: none;

    
}


h2 {
	
	font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    display: flex;
    align-items: center;
    color: #0E0E0F;
}

h3 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    text-transform: uppercase;
    color: #00B9B6;
}


h4 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: #0E0E0F;
}

h5 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: #0E0E0F;
}

h6 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    display: flex;
    align-items: center;
    color: #00B9B6;
}

.content {
	position: relative;
	margin: 0 auto;
	max-width: calc(1120px + 32px * 2);
    width: 100%;
	padding: 0 32px;
    display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

button {
   
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 30px 12px;
    gap: 10px;

    background: linear-gradient(225deg, #00B9B6 0%, #00CDAC 100%);
    box-shadow: 0px 15px 30px rgba(220, 232, 243, 0.25);
    border-radius: 12px;
    border: none;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: #FFFFFF;
}